/* Tabs.css */
.tabs-container {
    width: 100%;
    border: 0.1rem inset var(--swatch-09);
    border-radius: 0.4rem;
    overflow: hidden;
    background-color: var(--background-01);
  }
  
  .tabs-headers {
    display: flex;
    border-bottom: 0.1rem solid var(--swatch-09);
    background-color: var(--swatch-11);
  }
  
  .tab-header {
    flex: 1;
    padding: 1rem;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-align: center;
    font-size: 1rem;
    transition: background-color 0.3s, color 0.3s;
    color: var(--swatch-08);
    font-family: var(--font-02);
  }
  
  .tab-header:hover {
    background-color: var(--swatch-10);
  }
  
  .tab-header.active {
    background-color: var(--swatch-10);
    border-bottom: 0.2rem solid var(--swatch-07);
    font-weight: var(--weight-03);
    color: var(--swatch-07);
  }
  
  .tabs-content {
    padding: 2rem;
    background-color: var(--swatch-10);
    color: var(--swatch-04); /* Ensure text color is set */
  }
  
  .tab-content {
    display: none;
  }
  
  .tab-content.active {
    display: block;
  }
  
  .login-input {
    display: flex;
}

.login-input-toggle {
    width: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.login-input-toggle:hover {
    color: var(--swatch-07);
}

.app-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .tabs-wrapper {
    padding: 1rem;
    border-radius: 1rem;
    width: 33rem;
  }

  .color-picker-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1.25rem; /* 20px */
    background-color: #003366; /* Adjust to match your theme */
    border-radius: 0.625rem; /* 10px */
  }
  
  .color-display {
    margin-top: 0.625rem; /* 10px */
    padding: 0.625rem; /* 10px */
    border: 0.0625rem solid #fff; /* 1px */
    border-radius: 0.3125rem; /* 5px */
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

  .chrome-picker input {
    color: var(--swatch-04) !important;
  }