#image-slider {
    
}

#image-justifications {
    position: relative; 
    width: 100%; 
    height: 100vh; 
    overflow: hidden;
}

#image-justifications img {
    width: 100%; 
    height: 100%; 
    object-fit: cover;
}

.text-image {
    position: absolute;
    top: 0%;
    left: 0%;
    text-align: center;
    color: white; /* Text color */
    width: 50vw;
    height: calc(100vh - 2rem);
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding: 1rem;
    flex-direction: column;
    backdrop-filter: blur(0.5rem)
}

.text-image h1 {
    font-size: 4.5rem;
    height: 10rem;
    margin: 0;
    display: flex;
    align-items: center;
    padding-top: 2rem;
}

.text-image p {
    margin: 0;
    max-height: 35vh;
    overflow-y: auto;
}

.text-image h1,
.text-image p {
    color: white;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
}

.arrow-icons {
    position: absolute;
    bottom: 6rem;
    width: 100%;
    font-size: 3rem;
    display: flex;
    justify-content: space-between;
}

.custom-button {
    background: none;
    background-color: transparent;
    border: none;
    color: white;
    text-align: center;
    font-size: 1.8rem;
    margin-left: 1rem;
    margin-right: 1rem;
    cursor: pointer;
      
}

.page-number {
    position: absolute;
    left: 1rem;
    bottom: 1rem;
    display: flex;
    justify-content: flex-start;
    width: 100%;
    /* margin-top: 3.8rem; */
}


